import * as React from 'react'
import _ from 'lodash'
import styled from 'styled-components'

import { Box } from '@components/UI'
import spinner from '@images/ui/loading_spinner_01.gif'

// STODO use same IFrame everywhere, search global for IFrame and iframe
const StyledIFrame = styled.iframe`
  background-image: url("${spinner}");   
  background-repeat: no-repeat;
  background-position: 50% 50%;
  border: 0;
`

class IFrame extends React.Component<
  { url: string; title: string; getHeight: Function },
  { width: number; height: number }
> {
  container = React.createRef<HTMLElement>()

  state = { width: 0, height: 0 }

  updateDimensions = _.throttle(
    () => {
      const { width } = this.state
      const { getHeight } = this.props
      const newWidth = _.get(this.container, 'current.offsetWidth', width)

      if (newWidth !== width) {
        const height = getHeight(newWidth)
        this.setState({ width: newWidth, height })
      }
    },
    200,
    { leading: false, trailing: true }
  )

  // STODO how to reuse throttled function on window resize on other components?
  componentDidMount() {
    this.updateDimensions()
    window.addEventListener('resize', this.updateDimensions)
  }

  render() {
    const { title, url } = this.props
    const { height } = this.state

    return (
      <Box ref={this.container}>
        <StyledIFrame title={title} width="100%" height={height} src={url} />
      </Box>
    )
  }
}

export default IFrame
